// --- 活动函数 ---
export async function getEvents(): Promise<Event[]> {
  await new Promise(resolve => setTimeout(resolve, 500));
  return JSON.parse(JSON.stringify(mockEvents));
}

export async function getEventById(id: string): Promise<Event | undefined> {
  await new Promise(resolve => setTimeout(resolve, 300));
  const event = mockEvents.find(e => e.id === id);
  return event ? JSON.parse(JSON.stringify(event)) : undefined;
}

export function getEventStatus(eventDate: string): EventStatus {
  const date = parseISO(eventDate);
  const today = new Date();
  
  if (isEqual(new Date(date.toDateString()), new Date(today.toDateString()))) {
    return 'current';
  }
  if (isPast(date) && !isEqual(new Date(date.toDateString()), new Date(today.toDateString()))) {
    return 'past';
  }
  if (isFuture(date)) {
    return 'upcoming';
  }
  return 'current';
}

export async function createEvent(eventData: Omit<Event, 'id' | 'participants' | 'organizerId'>, organizerId: string): Promise<Event> {
  await new Promise(resolve => setTimeout(resolve, 500));
  const newEvent: Event = {
    ...eventData,
    id: `event${mockEvents.length + 1}`,
    participants: [],
    organizerId,
  };
  mockEvents.push(newEvent);
  return JSON.parse(JSON.stringify(newEvent));
}

export async function joinEvent(userId: string, eventId: string, cdKey: string): Promise<{ success: boolean, message: string, event?: Event }> {
  await new Promise(resolve => setTimeout(resolve, 300));
  const event = mockEvents.find(e => e.id === eventId);
  const user = mockUsers.find(u => u.id === userId);

  if (!event) return { success: false, message: '活动未找到。' };
  if (!user) return { success: false, message: '用户未找到。' };
  if (event.cdKey.toLowerCase() !== cdKey.toLowerCase()) return { success: false, message: '无效的激活码。' }; // cdKey comparison should be case-insensitive
  if (event.participantLimit && event.participants.length >= event.participantLimit) {
    return { success: false, message: '活动人数已满。' };
  }
  if (event.participants.some(p => p.id === userId)) {
    return { success: false, message: '您已加入此活动。', event };
  }

  const participant: Participant = { ...user, isCheckedIn: false };
  event.participants.push(participant);
  user.joinedEventIds = [...(user.joinedEventIds || []), eventId];
  
  return { success: true, message: '成功加入活动！', event: JSON.parse(JSON.stringify(event)) };
}

export async function leaveEvent(userId: string, eventId: string): Promise<{ success: boolean, message: string, event?: Event }> {
  await new Promise(resolve => setTimeout(resolve, 300));
  const event = mockEvents.find(e => e.id === eventId);
  const user = mockUsers.find(u => u.id === userId);

  if (!event) return { success: false, message: '活动未找到。' };
  if (!user) return { success: false, message: '用户未找到。' };

  const participantIndex = event.participants.findIndex(p => p.id === userId);
  if (participantIndex === -1) {
    return { success: false, message: '您当前未加入此活动。' };
  }

  event.participants.splice(participantIndex, 1);
  user.joinedEventIds = (user.joinedEventIds || []).filter(id => id !== eventId);

  return { success: true, message: '成功离开活动。', event: JSON.parse(JSON.stringify(event)) };
}

export async function checkInUser(userId: string, eventId: string): Promise<{ success: boolean, message: string }> {
  await new Promise(resolve => setTimeout(resolve, 300));
  const event = mockEvents.find(e => e.id === eventId);
  if (!event) return { success: false, message: '活动未找到。' };

  const participant = event.participants.find(p => p.id === userId);
  if (!participant) return { success: false, message: '您未报名参加此活动。' };
  if (participant.isCheckedIn) return { success: false, message: '您已签到。' };
  
  const eventStatus = getEventStatus(event.date);
  if (eventStatus !== 'current') {
    return { success: false, message: '此活动的签到尚未开始或已结束。' };
  }

  participant.isCheckedIn = true;
  return { success: true, message: '签到成功！' };
}


