'use server';

import { cookies } from 'next/headers';
import { redirect } from 'next/navigation';
import { z } from 'zod';
import {
  // mockOAuthLogin, // 原有的登录函数，可能需要修改或替换
  createEvent as apiCreateEvent,
  joinEvent as apiJoinEvent,
  leaveEvent as apiLeaveEvent,
  checkInUser as apiCheckInUser,
  ADMIN_CREATE_EVENT_PASSWORD,
  getMockUserById,
} from './data';
import type { Event } from './types';

const MAX_AGE = 60 * 60 * 24 * 30; // 30 天

// --- 认证操作 ---

// 注意：实际的OAuth登录流程会更复杂，涉及到与提供商的交互和回调处理。
// 此处 login 函数暂时保留，但登录页面的UI已更改为OAuth按钮。
// 如果要实现OAuth，需要新的action，例如 loginWithProvider(providerName: string)
export async function login(prevState: any, formData: FormData) {
  // 此函数目前与新的登录页面UI不匹配，因为UI现在使用OAuth按钮。
  // 如果保留基于表单的登录（例如用于特定管理员），则此逻辑可能仍需要。
  // 否则，应将其删除或重构为处理OAuth回调或启动OAuth流程。
  const email = formData.get('email') as string; 
  const password = formData.get('password') as string;

  if (!email || !password) {
    return { message: '用户名和密码为必填项。' };
  }
  
  // 假设 mockOAuthLogin 仍然适用，或者需要一个不同的验证逻辑
  // const user = await mockOAuthLogin(email, password);

  // 模拟一个用户查找，实际应用中需要对接OAuth提供商
  const users = [{id: 'user1', username: 'Steve'}, {id: 'user2', username: 'Alex'}]; // 来自 data.ts 的模拟用户
  const user = users.find(u => u.username.toLowerCase() === email.toLowerCase());


  if (user) {
    cookies().set('userId', user.id, {
      httpOnly: true,
      secure: process.env.NODE_ENV === 'production',
      maxAge: MAX_AGE,
      path: '/',
      sameSite: 'lax',
    });
    redirect('/profile');
  } else {
    return { message: '无效的用户名或密码。' };
  }
}


export async function logout() {
  cookies().delete('userId');
  redirect('/login');
}

export async function getSessionUser() {
  const userId = cookies().get('userId')?.value;
  if (!userId) return null;
  try {
    // TODO: 如果是OAuth登录，这里可能需要从会话中获取用户信息，而不是直接用ID查模拟用户
    return await getMockUserById(userId);
  } catch (error) {
    console.error("获取会话用户失败:", error);
    return null;
  }
}

// --- 活动操作 ---
const CreateEventSchema = z.object({
  name: z.string().min(3, "活动名称至少需要3个字符。"),
  date: z.string().refine((date) => !isNaN(Date.parse(date)), "无效的日期格式。"),
  description: z.string().min(10, "描述至少需要10个字符。"),
  participantLimit: z.preprocess(
    (val) => (val === "" || val === null || val === undefined ? undefined : Number(val)),
    z.number().int().positive().optional()
  ),
  cdKey: z.string().min(4, "激活码至少需要4个字符。").max(20),
  imageUrl: z.string().url("无效的图片链接。").optional().or(z.literal('')),
  adminPassword: z.string().refine(pass => pass === ADMIN_CREATE_EVENT_PASSWORD, "无效的管理员密码。")
});

export async function createEventAction(prevState: any, formData: FormData) {
  const rawFormData = Object.fromEntries(formData.entries());
  const validatedFields = CreateEventSchema.safeParse(rawFormData);

  if (!validatedFields.success) {
    return {
      errors: validatedFields.error.flatten().fieldErrors,
      message: '创建活动失败。请检查所填字段。',
    };
  }
  
  const user = await getSessionUser();
  if (!user) {
    return { message: '需要认证。' };
  }

  try {
    const { adminPassword, ...eventData } = validatedFields.data;
    const newEvent = await apiCreateEvent(eventData as Omit<Event, 'id' | 'participants' | 'organizerId'>, user.id);
    redirect(`/events/${newEvent.id}`);
  } catch (error) {
    console.error("创建活动错误:", error);
    return { message: '在服务器上创建活动失败。' };
  }
}

export async function joinEventAction(eventId: string, cdKey: string) {
  const user = await getSessionUser();
  if (!user) {
    return { success: false, message: '您必须登录才能加入活动。' };
  }
  try {
    const result = await apiJoinEvent(user.id, eventId, cdKey);
    return result;
  } catch (error) {
    return { success: false, message: '加入活动时发生错误。' };
  }
}

