import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import type { User } from "@/lib/types";

interface UserAvatarProps {
  user: Pick<User, 'username' | 'avatarUrl'>;
  className?: string;
}

export default function UserAvatar({ user, className }: UserAvatarProps) {
  const fallbackText = user.username ? user.username.substring(0, 2).toUpperCase() : "??";
  const placeholderSrc = user.avatarUrl || `https://picsum.photos/seed/${user.username}_minecraft_avatar/40/40`;

  return (
    <Avatar className={className}>
      <AvatarImage src={placeholderSrc} alt={user.username} data-ai-hint="minecraft avatar" />
      <AvatarFallback>{fallbackText}</AvatarFallback>
    </Avatar>
  );
}
